import React from "react";
import arrow from "assets/img/icons/black/light-fill/arrow-down.svg";
import { Group, Select, IconWrapper, Icon, InputError } from "./elements";

const EnhancedSelect = ({
  name,
  handleChange,
  id,
  autoFocus = true,
  disabled = false,
  form,
  multiple = false,
  required = false,
  size = 0,
  options,
  error,
  touched,
}) => (
  <Group>
    <Select
      autoFocus={autoFocus}
      name={name}
      disabled={disabled}
      form={form}
      multiple={multiple}
      required={required}
      size={size}
      id={id}
      onChange={handleChange}
    >
      {options.map((option) => (
        <option
          key={`${name}-${option.value}`}
          value={option.value}
          disabled={option.disabled}
          selected={option.selected}
        >
          {option.description}
        </option>
      ))}
    </Select>
    {!!error && touched && <InputError>{error}</InputError>}
    <IconWrapper>
      <Icon src={arrow} />
    </IconWrapper>
  </Group>
);

export default EnhancedSelect;
