import React from "react";

import Layout from "components/layout";
import CompanyTypes from "utils/companyTypes";
import CallToAction from "components/callToAction";
import Hero from "components/partner-landing-page/affiliateCustomizedStartupTemplate/hero";
import Rewards from "components/startups/rewards";
import FAQ from "components/startups/faq";
import SimpleValueProps from "components/startups/simpleValueProps";
import SideBySide from "components/ecommerce/SideBySide";
import Header from "components/partner-landing-page/header";
import {
  underwritingMessages,
  trackSpendingMessages,
} from "components/startups/messages";
import Setup from "components/startups/setup";
import underwriting from "assets/img/underwriting.png";
import receiptCapture from "assets/img/receipt-capture.png";

import brexLogo from "assets/img/logos/brex/logo-white.svg";

const AffiliateCustomizedStartup = ({
  pageContext: { contentfulData },
  location,
}) => {
  const designComponents = contentfulData.pageDesign.fields;
  const headerLogo = designComponents.brexAffiliateLogo
    ? designComponents.brexAffiliateLogo.fields.file.url
    : brexLogo;
  return (
    <Layout
      location={location}
      whiteContentBackground
      companyType={CompanyTypes.STARTUP}
      hideDefaultHeader
      title={contentfulData.siteHeadTitle}
      description={contentfulData.siteHeadDescription}
      refCode={contentfulData.refCode}
      indexPage={false}
    >
      <Header logo={headerLogo} />
      <Hero
        title={designComponents.heroTitle}
        subtitle={designComponents.heroSubtitle}
        footnote={designComponents.heroFootnote}
      />
      <Rewards upmarket={designComponents.upmarketPartner} />
      <SimpleValueProps />
      <SideBySide
        id="underwriting"
        messages={underwritingMessages}
        backgroundImage={underwriting}
      />
      <Setup />
      <SideBySide
        id="trackSpend"
        messages={trackSpendingMessages}
        backgroundImage={receiptCapture}
      />
      <FAQ />
      <CallToAction
        companyType={CompanyTypes.STARTUP}
        ctaText="GET STARTED NOW"
        analyticsPath={`${contentfulData.refCode}.bottomCTA.signup`}
      />
    </Layout>
  );
};

export default AffiliateCustomizedStartup;
