import React from "react";
import classnames from "classnames";
import brexCardBlank from "assets/img/mc.png";
import * as ramda from "ramda";
import SignUpReferenceLink from "components/reusable/atoms/links/SignUpReferenceLink";
import CompanyTypes from "utils/companyTypes";

const addNewLines = (lines) => {
  if (typeof lines === "string") return lines;

  return ramda.intersperse(
    <span>
      <br />
      <br />
    </span>,
    lines,
  );
};

const Intro = ({
  caption = "",
  extraActionButton,
  isLarge,
  noCard,
  className,
  ctaCaption = "Sign Up",
  companyType = CompanyTypes.STARTUP,
  heroSubtitle = "Signing up will not affect your credit score.",
}) => {
  const captionValue = typeof caption === "string" ? [caption] : caption;
  const totalCaptionLength = ramda.pipe(
    ramda.map(ramda.length),
    ramda.reduce(ramda.add, 0),
  )(captionValue);
  const isLongTitle = totalCaptionLength > 35;
  return (
    <section
      className={classnames(
        "partner-landing-intro",
        { "partner-landing-intro__larger": isLarge },
        { "partner-landing-intro__center": noCard },
        className,
      )}
    >
      <div
        className={classnames("promotion-text", {
          "promotion-text__small": isLongTitle,
        })}
      >
        {addNewLines(captionValue)}
        <br />
        <span>
          <SignUpReferenceLink
            companyType={companyType}
            className={classnames(
              "orange-button",
              "orange-button--getcard",
              "personalized-landing--headerbutton",
              { "personalized-landing--floatcenter": noCard },
            )}
            role="button"
          >
            {ctaCaption}
          </SignUpReferenceLink>
          {extraActionButton}
        </span>
        <span className="personalized-landing--headerSubText">
          {heroSubtitle}
        </span>
      </div>
      {!noCard && (
        <div className="brex-card">
          <img alt="Brex Card" src={brexCardBlank} />
        </div>
      )}
    </section>
  );
};

export default Intro;
