import React from "react";

import Header from "components/partner-landing-page/header";
import Intro from "components/partner-landing-page/intro.jsx";
import Description from "components/personalized-landing-page/description.jsx";
import Logos from "components/personalized-landing-page/logos.jsx";
import Footer from "components/footer/index";
import Layout from "components/layout";
import { createBody } from "utils/contentful";
import CompanyTypes from "utils/companyTypes";

export default ({ pageContext: { contentfulData }, location }) => {
  const companyType =
    contentfulData.urlPath === "yc" ? null : CompanyTypes.STARTUP;
  const designComponents = contentfulData.pageDesign.fields;
  return (
    <Layout
      location={location}
      title={contentfulData.siteHeadTitle}
      companyType={companyType}
      description={contentfulData.siteHeadDescription}
      refCode={contentfulData.refCode}
      hideDefaultHeader
      hideDefaultFooter
      indexPage={false}
    >
      <div className="partner-landing">
        <Header />
        <Intro
          companyType={companyType}
          caption={designComponents.heroTitle}
          ctaCaption={designComponents.heroButtonCta}
          className="partner-landing--personalized-intro"
          heroSubtitle={designComponents.heroSubtitle}
        />
        <Description
          companyType={companyType}
          title={designComponents.descriptionTitle}
          descriptionText={createBody(designComponents.descriptionBody, false)}
          logoImg={designComponents.brexAffiliateLogo.fields.file.url}
          logoClass="small-logo"
          ctaCaption={designComponents.descriptionButtonCta}
          learnMoreCta
          legalNotice={designComponents.legalNotice}
        />
        <Logos />
      </div>
      <Footer />
    </Layout>
  );
};
