import React from "react";
import qs from "qs";
import { path, isEmpty, isNil } from "ramda";

import {
  lifecycle,
  compose,
  withStateHandlers,
  branch,
  renderComponent,
} from "recompose";

import PassportAffiliate from "components/partner-landing-page/startupAffiliateTemplate.jsx";
import VCTemplate from "components/partner-landing-page/vcTemplate.jsx";
import EcommerceAffiliate from "components/partner-landing-page/ecommerceAffiliateTemplate.jsx";
import EcommerceBigPartner from "components/partner-landing-page/ecommerceBigPartnerTemplate.jsx";
import EcommerceBigPartnerWithoutForm from "components/partner-landing-page/ecommerceBigPartnerWithoutFormTemplate/index.jsx";
import DetailedStartupAffiliate from "components/partner-landing-page/detailedAffiliateTemplate.jsx";
import LifeSciencesAffiliate from "components/partner-landing-page/lifeSciencesTemplate.jsx";
import AffiliateCustomizedStartup from "components/partner-landing-page/affiliateCustomizedStartupTemplate/index.jsx";

import { getPreviewClient } from "../utils";

import { MessageWrapper } from "./elements";

// By expanding on the values of this map, marketing will be able to change
// landing page design straight from contentful.
// NOTE: ANY CHANGES HERE SHOULD BE REFLECTED ON THE MAP IN
// gatsby-node.js
const mapDesignToComponent = {
  "Passport Affiliate": PassportAffiliate,
  "First Tier VC": VCTemplate,
  "Second Tier VC": VCTemplate,
  "Ecommerce Affiliate": EcommerceAffiliate,
  "Ecommerce Big Partner": EcommerceBigPartner,
  "Ecommerce Big Partner Without Form": EcommerceBigPartnerWithoutForm,
  "Detailed Startup Affiliate": DetailedStartupAffiliate,
  "Life Sciences Affiliate": LifeSciencesAffiliate,
  "Affiliate Customized Startup": AffiliateCustomizedStartup,
};

const ContentfulPreview = ({ pageData, location }) => {
  const Component =
    mapDesignToComponent[pageData.fields.pageDesign.fields.designId];

  return (
    <Component
      pageContext={{ contentfulData: pageData.fields }}
      location={location}
    />
  );
};

const _lifecycle = {
  async componentDidMount() {
    const landingPageSpaceClient = getPreviewClient(
      process.env.CONTENTFUL_LANDING_PAGE_SPACE_ACCESS_TOKEN_PREVIEW,
      process.env.CONTENTFUL_LANDING_PAGE_SPACE_ID,
    );

    try {
      const urlSearch = qs.parse(window.location.search, {
        ignoreQueryPrefix: true,
      });

      const url = path(["url"], urlSearch);

      if (isNil(url)) {
        throw Error(
          "Please specify a URL to test via a URL parameter (i.e. www.brex.com/contentful-preview?url=yc)",
        );
      }

      const entries = await landingPageSpaceClient.getEntries({
        content_type: "landingPage",
        "fields.urlPath": url,
      });

      if (isEmpty(entries.items)) {
        throw Error(
          "The URL path that was provide was not matched to a contentful landing page. Please try again with a different path.",
        );
      }

      this.props.setPageData(entries.items[0]);
    } catch (error) {
      this.props.setError(error.message);
    }
  },
};

export default compose(
  withStateHandlers(
    {
      pageData: null,
      error: null,
    },
    {
      setPageData: () => (data) => ({ pageData: data }),
      setError: () => (errorMessage) => ({ error: errorMessage }),
    },
  ),
  lifecycle(_lifecycle),
  branch(
    ({ error }) => !!error,
    renderComponent(({ error }) => <MessageWrapper>{error}</MessageWrapper>),
  ),
  branch(
    ({ pageData }) => !pageData,
    renderComponent(() => (
      <MessageWrapper>Fetching contentful data...</MessageWrapper>
    )),
  ),
)(ContentfulPreview);
