import React from "react";

import { InputError, InputWrapper, Input } from "./elements";

const StyledInput = ({
  type,
  name,
  placeholder,
  onBlur,
  onChange,
  value,
  error,
  touched,
  id,
}) => (
  <InputWrapper type={type}>
    <Input
      type={type}
      name={name}
      placeholder={placeholder}
      onBlur={onBlur}
      onChange={onChange}
      value={value}
      error={touched && error}
      id={id}
    />
    {!!error && touched && <InputError>{error}</InputError>}
  </InputWrapper>
);

export default StyledInput;
