import styled from "@emotion/styled";
import colors from "styles/colors";
import { breakPoints } from "components/Grid.tsx";

export const Input = styled("input")(({ error }) => [
  {
    width: "100%",
    height: 45,
    marginBottom: 16,
    paddingLeft: 10,

    [breakPoints.small]: {
      height: 45,
    },
  },
  error && {
    outlineColor: colors.warning,
    outlineStyle: "solid",
    outlineWidth: 1,
  },
]);

export const InputError = styled("span")({
  position: "absolute",
  fontSize: 12,
  color: colors.warning,
  right: 0,
  paddingTop: 5,
  paddingRight: 10,
});

export const InputWrapper = styled("div")(({ type }) => [
  {
    position: "relative",
    width: "100%",
  },

  type === "hidden" && {
    display: "none",
  },
]);
