// Base component to created the TrustedBy section
// Assumes logos are passed on

import React from "react";
import BrandLogo from "components/reusable/atoms/BrandLogo";

const TrustedByBase = ({ customers, title = "Trusted By" }) => (
  <div className="trusted-by pt-3 pt-md-5">
    <h5 className="trusted-by__title">{title}</h5>
    <div className="trusted-by__logos">
      {customers.map(({ logo, key, height, width }) => (
        <div className="trusted-by__logo" key={key}>
          <BrandLogo logo={logo} height={height} width={width} />
        </div>
      ))}
    </div>
  </div>
);

export default TrustedByBase;
